* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  color: #333;
  background-color: #f9f9f9;
  line-height: 1.6;
}

h1, h2, h3 {
  margin-bottom: 1rem;
}

main {
  padding: 2rem;
}

section {
  margin-bottom: 4rem;
}

a {
  color: #0077cc;
  text-decoration: none;
}

a:hover {
  color: #005fa3;
}